<!--前端测试文件-->
<template>
    <section id="test1">
        <textarea ref="mycode" class="codesql" v-model="item.code" style="width:100%;height:100%;"></textarea>
    </section>
</template>

<script>
import 'codemirror/theme/ambiance.css'
import 'codemirror/lib/codemirror.css'
import 'codemirror/addon/hint/show-hint.css'

let CodeMirror = require('codemirror/lib/codemirror')
require('codemirror/addon/edit/matchbrackets')
require('codemirror/addon/selection/active-line')
require('codemirror/mode/javascript/javascript.js')
require('codemirror/mode/sql/sql.js')
require('codemirror/mode/sql/sql')
require('codemirror/addon/hint/show-hint')
require('codemirror/addon/hint/sql-hint')
export default {
    name: 'test2',
    data () {
        return {
            editor: null,
            codes: 'echo 1',
            taskContent: '',
            dayProjectName: 'zz',
            dayJobName: 'test1',
            result: { 'saveParamsResult': { 'code': 0, 'message': '' }, 'saveDependenceResult': { 'code': 0, 'message': '' }, 'saveScheduleResult': { 'code': 0, 'message': '' }, 'saveContentResult': { 'code': 0, 'message': '' }, 'uploadFlowResult': { 'code': 0, 'message': '' } },
            item: {
                code: ''
            },
            proJect: '',
            jobNames: ''
        }
    },
    watch: {
        taskContent: {
            immediate: true,
            handler: function () {
                if (this.editor) {
                    this.stakContent()
                }
            }
        },
        codes: {
            immediate: true,
            handler: function () {
                if (this.editor) {
                    this.editor.setValue(this.codes)
                }
            }
        },
        dayProjectName () {
            this.proJect = this.dayProjectName
        },
        dayJobName () {
            this.jobNames = this.dayJobName
        }
    },
    mounted () {
        this.start()
    },
    methods: {
        start () {
            this.editor = CodeMirror.fromTextArea(this.$refs.mycode, {
                mode: 'sql',
                theme: 'ambiance',
                indentWithTabs: true,
                smartIndent: true,
                lineNumbers: true,
                matchBrackets: true,
                extraKeys: {'Ctrl': 'autocomplete'}, // 自定义快捷键
                hintOptions: {// 自定义提示选项
                    tables: {
                        users: ['name', 'score', 'birthDate'],
                        countries: ['name', 'population', 'size']
                    }
                }
            })
            setTimeout(() => {
                this.editor.on('cursorActivity', () => {
                    // console.log(this.editor.getValue())
                    this.item.code = this.editor.getValue()
                    // console.log(this.item.code)
                    this.editor.showHint()
                })
            }, 500)
        },
        stakContent () {
            var params = {
                projectName: this.dayProjectName,
                jobName: this.dayJobName,
                jobContent: this.editor.getValue()
            }
            // console.log(params)
        }
    }
}
</script>

<style lang="scss" scoped>
#test1{
    height: 100%;
}
</style>

<style>
.CodeMirror{
    height: 100%;
}
</style>



